@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
  font-size: 62.5%;

  // блокировка скролла
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked; }

  &.css-scroll-fixed.open-only-mob {
    @include media('<tab') {
      @include scroll-blocked; } } }

body {
  position: relative;
  @include font-normal;
  @include font($font-size, $line-height);
  color: $text-color;
  min-width: $page-layout-min;
  background: $bg-color url(../../img/empty.png) no-repeat left top fixed;

  // выделение текста
  *::selection {
    background-color: $base-color;
    color: white; } }

.max-wrap {
  max-width: $page-layout-width;
  @include push-auto; }

.layout {
  @if nth($page-layout-space, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space, 2)); } }

.layout-narrow {
  @if nth($page-layout-space-narrow, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space-narrow, 2));

    @include media('<lap-s') {
      @include padding-horizontal(nth($page-layout-space-narrow--tab, 2)); } } }

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  margin: 0 auto;
  //min-height: 100vh
  //min-height: 100% // not support ie
  //height: 100%
  //overflow: hidden
  //height: 100vh
  min-height: 100vh;
  position: relative; }
